.ChargesForm {
  padding: 5px;

  .errorMessage {
    color: red;
    font-size: 10px !important;
  }
  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
  }
  .previewOrStyling {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    color: #1e2528;
  }

  // .contentPart::-webkit-scrollbar {
  //   width: 0.3em;
  // }

  // .contentPart::-webkit-scrollbar-track {
  //   box-shadow: inset 0 0 6px rgba(91, 90, 90, 0.2);
  //   border-radius: 5%;
  // }

  // .contentPart::-webkit-scrollbar-thumb {
  //   background-color:transparent;
  //   outline: 0.2px solid rgb(183, 186, 189);
  //   border-radius: 5%;
  // }

  .timingsHeader {
    color: #090909;
  }
  .basicTitle {
    color: #090909;
    font-size: 16px;
    font-weight: 700;
    .standardBlack {
      color: #1e2528;
      font-size: 16px;
      font-weight: 700;
    }
    .headingBlack {
      color: #090909;
      font-size: 16px;
      font-weight: 700;
    }
  }
  .radioLabelStyle {
    font-family: Barlow;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    color: #1e2528;
  }

  .linkText {
    font-size: 12px;
    font-weight: 500;
    color: #1e2528;
    line-height: 14px;
    margin: 0px;
  }
  .earlyCheckInChargesBody {
    border: 1px solid #f6f6f6;
    border-radius: 16px;
    height: 100%;

    .labelHeader {
      background-color: #2a5067;
      color: #fff;
      font-size: 14px;
      font-weight: 600;
      width: 100%;
      border-top-right-radius: 16px;
      border-top-left-radius: 16px;
      height: 48px;
      display: flex;
      align-items: center;
      padding: 0px 15px;
      margin: 0;
    }
    .heading {
      background-color: #f2faff;
      width: 100%;
      font-size: 14px;
      color: #2a5067;
      font-weight: 600;
      height: 48px;
      display: flex;
      align-items: center;
      padding: 0px 15px;
      line-height: 20px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 0px;
    }
    .disabledinputfield {
      background-color: #1e2528;
      cursor: none;
    }

    /* Define the styles for even rows */
    .earlyCheckEvenrow {
      height: 44px;
      display: flex;
      align-items: center;
      padding-left: 20px;
      width: 100%;
    }
    .earlyCheckOddrow {
      padding-left: 20px;
      width: 100%;
      height: 44px;
      display: flex;
      align-items: center;
      background-color: #f6f6f6;
    }
    .earlyCheckLastItem:nth-child(even) {
      background-color: #f6f6f6;
      border-bottom-left-radius: 16px;
      border-bottom-right-radius: 16px;
      width: 100%;
      padding-left: 20px;
      height: 44px;
      display: flex;
      align-items: center;
    }
    .earlyCheckLastItem:nth-child(odd) {
      // background-color: #f6f6f6;
      border-bottom-left-radius: 16px;
      border-bottom-right-radius: 16px;
      width: 100%;
      padding-left: 20px;
      height: 44px;
      display: flex;
      align-items: center;
    }
  }
  .timelinessWidth {
    width: 265px;
    display: flex;
    flex-direction: column;
  }
  .btnWidth {
    width: 160px;
  }
  .or {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    color: #a5a8a9;
    margin: 14px 20px;
  }
  .newChargeType {
    color: #935ed6;
    margin: 8px;
    font-size: 13px;
    font-weight: 500;
  }
  hr {
    border-top: 2px solid #e8e9e9;
  }
  .btn {
    column-gap: 17px;
  }
  .errorMessage {
    font-size: 10px;
  }
}
